.sas-va-sdk .sas_components-BusyStateIndicator-BusyStateAnimationElement_third, .sas-va-sdk .sas_components-BusyStateIndicator-BusyStateAnimationElement_second, .sas-va-sdk .sas_components-BusyStateIndicator-BusyStateAnimationElement_first {
  display: inline-block;
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  overflow: hidden;
  animation: sas_components-BusyStateIndicator-BusyStateAnimationElement_grow calc(1.8s * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0.16s infinite;
  animation-fill-mode: both;
  vertical-align: middle;
  background-color: var(--busy-state-dot-bg);
}

.sas-va-sdk .sas_components-BusyStateIndicator-BusyStateAnimationElement_second {
  animation-delay: 0.32s;
}

.sas-va-sdk .sas_components-BusyStateIndicator-BusyStateAnimationElement_third {
