@keyframes novaSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes novaFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes novaFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes novaSlideUpFadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideDownFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(8px);
  }
}
@keyframes novaSlideLeftFadeIn {
  from {
    opacity: 0;
    transform: translateX(16px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideLeftFadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-16px);
  }
}
@keyframes novaPopIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes novaPopOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}
@keyframes novaSlideInStart {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutStart {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}
@keyframes novaSlideInEnd {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutEnd {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100px);
  }
}
@keyframes novaSlideInTop {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}
@keyframes novaSlideInBottom {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutBottom {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
@keyframes novaBounceInLeft {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }
  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }
  100% {
    transform: none;
  }
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_container {
  width: 100%;
  position: relative;
  box-sizing: border-box;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_section {
  display: flex;
  flex-direction: column;
  position: relative;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_section-header {
  display: flex;
  align-items: center;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_heading-layout {
  flex-grow: 1;
  min-width: 0;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_heading {
  font-size: var(--windowshade-font-size-h5);
  font-weight: var(--windowshade-font-weight-default);
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_header-action {
  flex-shrink: 0;
  min-width: 0;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_header {
  height: 2.25rem;
  display: flex;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  position: relative;
  padding: var(--windowshade-spacing-s) 0;
  box-sizing: border-box;
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_header:focus {
  outline: var(--windowshade-header-focus);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_header-wrap {
  height: auto;
  min-height: 2rem;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_disabled {
  cursor: default;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_header-icon {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  flex-grow: 0;
  color: var(--windowshade-header-fg);
  position: absolute;
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_header-icon-open {
  transform: translate3d(0, 0, 0) rotate(90deg);
}
.sas-va-sdk[dir=rtl] .sas_components-WindowShade-WindowShade_header-icon-open {
  transform: translate3d(0, 0, 0) rotate(-90deg);
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_header-icon-closed {
  transform: translate3d(0, 0, 0) rotate(0deg);
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_header-icon-transitioning {
  transition: transform calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
}

.sas-va-sdk[dir="ltr"] .sas_components-WindowShade-WindowShade_header-optional-icon {
  padding-left: calc(var(--windowshade-spacing-m) * 1.375);
}

.sas-va-sdk[dir="rtl"] .sas_components-WindowShade-WindowShade_header-optional-icon {
  padding-right: calc(var(--windowshade-spacing-m) * 1.375);
}

.sas-va-sdk[dir="ltr"] .sas_components-WindowShade-WindowShade_header-text {
  padding-left: var(--windowshade-padding-start);
}

.sas-va-sdk[dir="rtl"] .sas_components-WindowShade-WindowShade_header-text {
  padding-right: var(--windowshade-padding-start);
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_header-text {
  font-size: var(--windowshade-header-font-size-m);
  min-width: 0;
  color: var(--windowshade-header-fg);
}

.sas-va-sdk[dir="ltr"] .sas_components-WindowShade-WindowShade_content {
  padding-left: var(--windowshade-spacing-m);
}

.sas-va-sdk[dir="rtl"] .sas_components-WindowShade-WindowShade_content {
  padding-right: var(--windowshade-spacing-m);
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_content {
  overflow: auto;
  padding-top: var(--windowshade-spacing-xs);
  margin-bottom: var(--windowshade-spacing-s);
}
.sas-va-sdk .sas_components-WindowShade-WindowShade_content-transitioning {
  padding-bottom: 0;
  transition: height calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_draggable-item {
  cursor: default;
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_draggable-item:hover {
  background: var(--windowshade-hoverState-color);
  box-shadow: var(--windowshade-medium-sh);
  transition: box-shadow calc(var(--windowshade-trans-dur) * var(--nova-animation-duration-factor, 1)) var(--windowshade-anim-dur);
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_clone-img {
  opacity: var(--windowshade-dragged-opacity);
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_drag-img {
  box-shadow: var(--windowshade-medium-sh);
  cursor: default;
  background: var(--windowshade-hoverState-color);
}

.sas-va-sdk .dndIndicator {
  margin-top: 0.35rem;
}
.sas-va-sdk .dndIndicator,
.sas-va-sdk .dndBottomIndicator {
  background: var(--windowshade-indicator-line-bg);
  width: 100%;
  height: 2px;
  position: absolute;
  top: -4px;
  display: flex;
  justify-content: center;
  margin-bottom: 0.35rem;
}
.sas-va-sdk .dndBottomIndicator {
  top: auto;
}
.sas-va-sdk[dir="ltr"] .dndIndicator:before,.sas-va-sdk[dir="ltr"] 
.dndBottomIndicator:before {
  left: -1px;
}
.sas-va-sdk[dir="rtl"] .dndIndicator:before,.sas-va-sdk[dir="rtl"] 
.dndBottomIndicator:before {
  right: -1px;
}
.sas-va-sdk .dndIndicator:before,
.sas-va-sdk .dndBottomIndicator:before {
  content: "";
  position: absolute;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 0.25rem;
  border: 2px solid var(--windowshade-indicator-loop);
  background: var(--windowshade-indicator-line-bg);
  box-shadow: 0 0 4px 0 var(--windowshade-indicator-line-bg);
  top: -5px;
  pointer-events: none;
}

.sas-va-sdk[dir="ltr"] .sas_components-WindowShade-WindowShade_required-mark {
  margin-left: var(--windowshade-spacing-xxs);
}

.sas-va-sdk[dir="rtl"] .sas_components-WindowShade-WindowShade_required-mark {
  margin-right: var(--windowshade-spacing-xxs);
}

.sas-va-sdk .sas_components-WindowShade-WindowShade_required-mark {
  color: var(--windowshade-header-asterisk-fg);
}