.sas-va-sdk .sas_components-NumericStepper-NumericStepper_container {
  display: flex;
  align-items: center;
}

.sas-va-sdk .sas_components-NumericStepper-NumericStepper_input {
  height: 100%;
}

.sas-va-sdk .sas_components-NumericStepper-NumericStepper_input > input {
  text-align: center;
}

.sas-va-sdk .sas_components-NumericStepper-NumericStepper_has-status > input, .sas-va-sdk .sas_components-NumericStepper-NumericStepper_has-status > input:hover {
  border-color: var(--input-bd);
}
.sas-va-sdk[dir="ltr"] .sas_components-NumericStepper-NumericStepper_has-status > input:focus {
  padding-right: calc(2 * var(--input-spacing-xs));
}
.sas-va-sdk[dir="rtl"] .sas_components-NumericStepper-NumericStepper_has-status > input:focus {
  padding-left: calc(2 * var(--input-spacing-xs));
}
.sas-va-sdk .sas_components-NumericStepper-NumericStepper_has-status > input:focus {
  border-color: var(--input-bd);
}