.sas-va-sdk .sas_components-List-List_list {
  flex-grow: 1;
  min-height: 0;
}
.sas-va-sdk .sas_components-List-List_list .sas_components-List-List_list-inner {
  height: 100%;
  width: 100%;
}

.sas-va-sdk .sas_components-List-List_icon-column-static-icon {
  margin-top: var(--list-spacing-xs);
}

.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell {
  position: relative;
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell.sas_components-List-List_direction_ltr::before {
  left: 0;
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell.sas_components-List-List_direction_rtl::before {
  right: 0;
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell::before {
  content: "";
  border-top: var(--focus-width) solid transparent;
  border-bottom: var(--focus-width) solid transparent;
  width: calc(100% - var(--table-last-column-pd-adjustment, 0px) - 1px);
  height: 100%;
  top: 0;
  position: absolute;
  box-sizing: border-box;
  pointer-events: none;
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell.sas_components-List-List_direction_ltr:first-child::before {
  border-left: var(--focus-width) solid transparent;
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell.sas_components-List-List_direction_rtl:first-child::before {
  border-right: var(--focus-width) solid transparent;
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell.sas_components-List-List_direction_ltr:last-child::after {
  border-right: var(--focus-width) solid transparent;
  right: var(--table-last-column-pd-adjustment, 0px);
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell.sas_components-List-List_direction_rtl:last-child::after {
  border-left: var(--focus-width) solid transparent;
  left: var(--table-last-column-pd-adjustment, 0px);
}
.sas-va-sdk .sas_components-List-List_row-selection-border > .sas_components-List-List_cell:last-child::after {
  content: "";
  position: absolute;
  height: 100%;
  box-sizing: border-box;
}
.sas-va-sdk .sas_components-List-List_row-selection-border.sas_components-List-List_row-selection-border-selected > .sas_components-List-List_cell::before,
.sas-va-sdk .sas_components-List-List_row-selection-border.sas_components-List-List_row-selection-border-selected > .sas_components-List-List_cell:first-child::before,
.sas-va-sdk .sas_components-List-List_row-selection-border.sas_components-List-List_row-selection-border-selected > .sas_components-List-List_cell:last-child::after {
  border-color: var(--list-row-border-highlighting-color);
  z-index: 1;
}