}
@keyframes novaSlideInTop {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}
@keyframes novaSlideInBottom {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutBottom {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
@keyframes novaBounceInLeft {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }
  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }
  100% {
    transform: none;
  }
}
