.sas-va-sdk .sas_components-Slider-Slider_container_horizontal, .sas-va-sdk .sas_components-Slider-Slider_container_vertical {
  box-sizing: border-box;
  display: inline-grid;
  overflow: hidden;
}

.sas-va-sdk .sas_components-Slider-Slider_container_vertical {
  height: 100%;
  grid-template: ". . ." 1rem ". track ." 10px "tick-labels track labels" 1fr ". track. " 7px ". . ." 1.5rem/minmax(min-content, 0.25rem) 1rem min-content;
}

.sas-va-sdk .sas_components-Slider-Slider_container_horizontal {
  width: 100%;
  grid-template: ". . labels . ." min-content ". track track track ." 1rem ". . tick-labels . ." minmax(min-content, 0.25rem)/0.75rem 0px 1fr 0px 0.75rem;
}

.sas-va-sdk .sas_components-Slider-Slider_track_horizontal, .sas-va-sdk .sas_components-Slider-Slider_track_vertical {
  position: relative;
  box-sizing: border-box;
  background-color: var(--slider-track-bg);
  border-radius: 2px;
  border: 1px solid var(--slider-track-bd);
}

.sas-va-sdk[dir="ltr"] .sas_components-Slider-Slider_track_vertical {
  border-left-color: var(--slider-track-bd);
}

.sas-va-sdk[dir="rtl"] .sas_components-Slider-Slider_track_vertical {
  border-right-color: var(--slider-track-bd);
}

.sas-va-sdk[dir="ltr"] .sas_components-Slider-Slider_track_vertical {
  border-right-color: var(--slider-track-top-bd);
}

.sas-va-sdk[dir="rtl"] .sas_components-Slider-Slider_track_vertical {
  border-left-color: var(--slider-track-top-bd);
}

.sas-va-sdk .sas_components-Slider-Slider_track_vertical {
  display: inline-block;
  justify-self: center;
  width: 0.25rem;
  height: 100%;
  grid-row: 3;
  grid-column: 2;
}

.sas-va-sdk .sas_components-Slider-Slider_track_horizontal {
  align-self: center;
  height: 0.25rem;
  border-top-color: var(--slider-track-top-bd);
  border-bottom-color: var(--slider-track-bd);
  grid-row: 2;
  grid-column: 3;
}