@keyframes novaSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes novaFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes novaFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes novaSlideUpFadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideDownFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(8px);
  }
}
@keyframes novaSlideLeftFadeIn {
  from {
    opacity: 0;
    transform: translateX(16px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideLeftFadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-16px);
  }
}
@keyframes novaPopIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes novaPopOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}
@keyframes novaSlideInStart {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutStart {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}
@keyframes novaSlideInEnd {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutEnd {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100px);
  }
}
@keyframes novaSlideInTop {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}
@keyframes novaSlideInBottom {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutBottom {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
@keyframes novaBounceInLeft {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }
  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }
  100% {
    transform: none;
  }
}
.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_current {
  font-weight: var(--breadcrumb-text-font-weight-thick);
}

.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_interactive {
  cursor: pointer;
}

.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_interactive:hover {
  text-decoration: underline;
}

.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_item-text {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: var(--breadcrumb-text-font-size-m);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-decoration: none;
  cursor: default;
  color: var(--breadcrumb-text-fg);
  padding: var(--breadcrumb-squish-xs);
  display: block;
  font-family: var(--breadcrumb-text-font-family);
}

.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_item-text:focus {
  outline: var(--breadcrumb-text-focus);
  outline-offset: calc(-1 * var(--focus-width));
}

.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_animated {
  animation-name: sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_novaBounceInLeft;
  animation-duration: calc(250ms * var(--nova-animation-duration-factor, 1));
}

.sas-va-sdk .sas_components-Breadcrumb-BreadcrumbItem-SingleBreadcrumbDesign_single-item {
  display: flex;
  max-width: var(--breadcrumb-max-width);
}