.sas-va-sdk .sas_components-Toolbar-Toolbar_toolbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}
.sas-va-sdk[dir="ltr"] .sas_components-Toolbar-Toolbar_toolbar > * + * {
  margin-left: var(--toolbar-spacing-s);
}
.sas-va-sdk[dir="rtl"] .sas_components-Toolbar-Toolbar_toolbar > * + * {
  margin-right: var(--toolbar-spacing-s);
}
.sas-va-sdk[dir="ltr"] .sas_components-Toolbar-Toolbar_toolbar > * + * {
  margin-right: 0;
}
.sas-va-sdk[dir="rtl"] .sas_components-Toolbar-Toolbar_toolbar > * + * {
  margin-left: 0;
}

.sas-va-sdk .sas_components-Toolbar-Toolbar_separator-container {
  display: flex;
  align-self: stretch;
}

.sas-va-sdk .sas_components-Toolbar-Toolbar_spacer-container {
  display: contents;
  flex-grow: 1;
}