.sas_ObjectStatus-ObjectStatus_base, .sas_ObjectStatus-ObjectStatus_busy-single {
  height: 2rem;
  border-radius: 1rem;
  -webkit-padding-start: var(--objectstatus-padding);
          padding-inline-start: var(--objectstatus-padding);
  -webkit-padding-end: var(--objectstatus-padding);
          padding-inline-end: var(--objectstatus-padding);
  border-width: 1px;
  box-sizing: border-box;
  border-style: solid;
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  background-color: var(--objectstatus-bg);
  border-color: var(--objectstatus-bd);
  color: var(--objectstatus-fg);
}
.sas_ObjectStatus-ObjectStatus_base:focus, .sas_ObjectStatus-ObjectStatus_busy-single:focus {
  outline: var(--objectstatus-focus-outline);
  outline-offset: var(--objectstatus-focus-outline-offset);
}

.sas_ObjectStatus-ObjectStatus_text, .sas_ObjectStatus-ObjectStatus_busy-single {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  line-height: normal;
}

.sas_ObjectStatus-ObjectStatus_text {
  font-size: 0.8125rem;
}

.sas_ObjectStatus-ObjectStatus_icon {
  flex-shrink: 0;
}
