.sas-va-sdk .sas_components-Table-VirtualScrollbar_container {
  box-sizing: border-box;
  position: relative;
  margin: 0;
}

.sas-va-sdk .sas_components-Table-VirtualScrollbar_hbar {
  overflow-x: auto;
  overflow-y: hidden;
}

.sas-va-sdk .sas_components-Table-VirtualScrollbar_hbar .sas_components-Table-VirtualScrollbar_body {
  min-height: 1px;
}

.sas-va-sdk .sas_components-Table-VirtualScrollbar_vbar {
  overflow-x: hidden;
  overflow-y: auto;
}

.sas-va-sdk .sas_components-Table-VirtualScrollbar_vbar .sas_components-Table-VirtualScrollbar_body {
  min-width: 1px;
}