.sas-va-sdk .sas_components-Text-Text_text {
  color: var(--text-fg);
  display: inline-block;
  max-width: 100%;
  font-size: var(--text-font-size-m);
  white-space: pre-wrap;
  overflow-wrap: anywhere;
  word-break: break-word;
}

.sas-va-sdk .sas_components-Text-Text_text:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.sas-va-sdk .sas_components-Text-Text_nowrap {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
  overflow-wrap: unset;
  word-break: unset;
}

.sas-va-sdk .sas_components-Text-Text_has-max-lines {
  display: -webkit-inline-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}