.sas-va-sdk .sas_components-Indicator-Indicator_container {
  color: var(--indicator-fg);
  font-size: var(--indicator-font-size-m);
}

.sas-va-sdk .sas_components-Indicator-Indicator_processing {
  animation: novaSpin var(--indicator-processing-anim-duration) linear var(--indicator-processing-anim-iteration-count);
}

.sas-va-sdk .sas_components-Indicator-Indicator_alert {
  color: var(--indicator-alert-fg);
}

.sas-va-sdk .sas_components-Indicator-Indicator_ready {
  color: var(--indicator-ready-fg);
}