@keyframes novaSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes novaFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes novaFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes novaSlideUpFadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideDownFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(8px);
  }
}
@keyframes novaSlideLeftFadeIn {
  from {
    opacity: 0;
    transform: translateX(16px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideLeftFadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-16px);
  }
}
@keyframes novaPopIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes novaPopOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}
@keyframes novaSlideInStart {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutStart {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}
@keyframes novaSlideInEnd {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutEnd {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100px);
  }
}
@keyframes novaSlideInTop {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}
@keyframes novaSlideInBottom {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutBottom {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
@keyframes novaBounceInLeft {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }
  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }
  100% {
    transform: none;
  }
}
.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_avatar-container {
  cursor: pointer;
  overflow: hidden;
  padding: 0;
  border: 0;
  max-width: 100%;
  margin-bottom: var(--avatar-selector-spacing-m);
  position: relative;
}

.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_avatar {
  position: relative;
}
.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_avatar > * {
  color: var(--avatar-fg);
  background-color: var(--avatar-bg);
}

.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_avatar:focus {
  outline: none;
}

.sas-va-sdk[dir="ltr"] .sas_components-AvatarSelector-AvatarSelector_edit-icon {
  right: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-AvatarSelector-AvatarSelector_edit-icon {
  left: 0;
}

.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_edit-icon {
  position: absolute;
  bottom: 0;
  opacity: 0;
  transition: opacity calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
  background: var(--avatar-selector-edit-icon-bg);
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: solid 1px var(--avatar-selector-edit-icon-bd);
}

.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_avatar-container:hover .sas_components-AvatarSelector-AvatarSelector_edit-icon,
.sas-va-sdk .sas_components-AvatarSelector-AvatarSelector_avatar:focus + .sas_components-AvatarSelector-AvatarSelector_edit-icon {
  opacity: 1;
}