  margin-top: var(--menu-spacing-s);
  font-size: var(--menu-heading-size-medium);
  font-weight: var(--menu-font-weight-thick);
}

.sas-va-sdk .sas_components-Menu-__internal__-MenuListSection_title-disabled {
  color: var(--menu-disabled-color);
}

.sas-va-sdk .sas_components-Menu-__internal__-MenuListSection_separated {
