.sas-va-sdk .sas_components-Token-Token_token {
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  height: 1.5rem;
  padding: var(--token-spacing-squish-xs);
  border-style: solid;
  border-width: 1px;
  border-radius: 0.668rem;
  font-size: var(--token-font-size-s);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: var(--token-bg);
  border-color: var(--token-bd);
  color: var(--token-fg);
  max-width: 100%;
}

.sas-va-sdk .sas_components-Token-Token_text {
  font-size: var(--token-font-size-s);
  padding: var(--token-spacing-xxs);
}

.sas-va-sdk .sas_components-Token-Token_icon {
  height: 0.75rem;
  width: 0.75rem;
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_text-wrapper {
  margin-left: var(--token-spacing-xs);
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_text-wrapper {
  margin-right: var(--token-spacing-xs);
}

.sas-va-sdk .sas_components-Token-Token_text-wrapper {
  display: flex;
  align-items: center;
  flex-grow: 1;
  min-width: 1rem;
}
.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_text-wrapper > .sas_components-Token-Token_icon {
  margin-right: var(--token-spacing-xs);
}
.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_text-wrapper > .sas_components-Token-Token_icon {
  margin-left: var(--token-spacing-xs);
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_close-icon-container {
  margin-right: var(--token-spacing-xs);
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_close-icon-container {
  margin-left: var(--token-spacing-xs);
}

.sas-va-sdk .sas_components-Token-Token_close-icon-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  flex-shrink: 0;
}

.sas-va-sdk .sas_components-Token-Token_close-icon-focus-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sas-va-sdk .sas_components-Token-Token_close-icon-focus-container.sas_components-Token-Token_focus-visible:focus {
  outline-offset: calc(2px + var(--focus-width) * -1);
}

.sas-va-sdk .sas_components-Token-Token_focus-visible:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-2px + var(--focus-width) * -1);
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_group-token-wrapper {
  margin-left: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_group-token-wrapper {
  margin-right: 0;
}

.sas-va-sdk[dir="ltr"] .sas_components-Token-Token_group-token-close-icon-container {
  margin-left: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-Token-Token_group-token-close-icon-container {
  margin-right: 0;
}

.sas-va-sdk .sas_components-Token-Token_dropdown-open {
  cursor: pointer;
  background-color: var(--token-expanded-bg);
  filter: var(--token-expanded-filter);
}

.sas-va-sdk .sas_components-Token-Token_group-token-menu,
.sas-va-sdk .sas_components-Token-Token_group-token-menu:hover {
  background: transparent;
  border: transparent;
  filter: var(--token-active-filter);
}