.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover {
  pointer-events: initial;
  z-index: 20;
  display: grid;
  background-color: var(--popover-bg);
  box-shadow: var(--popover-sh);
  border-radius: var(--popover-border-radius);
}

.sas-va-sdk[dir="ltr"] .sas_components-Popover-__internal__-BasePopover_popover:focus::after {
  left: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-Popover-__internal__-BasePopover_popover:focus::after {
  right: 0;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover:focus::after {
  content: "";
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  pointer-events: none;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover-title {
  padding: var(--popover-padding);
  padding-bottom: 0;
  border: 1px solid var(--popover-bd);
  border-bottom: none;
  font-size: var(--popover-font-size-m);
  font-weight: var(--popover-title-font-weight);
  color: var(--popover-title-fg);
  overflow: hidden;
  justify-content: center;
  border-start-end-radius: var(--popover-border-radius);
  border-start-start-radius: var(--popover-border-radius);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover-pane {
  overflow: auto;
  box-sizing: border-box;
  display: inline-block;
  border: 1px solid var(--popover-bd);
  border-radius: var(--popover-border-radius);
  font-size: var(--popover-font-size-m);
  padding: var(--popover-padding);
}
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover-pane.sas_components-Popover-__internal__-BasePopover_hide-top-border {
  border-top: none;
  border-start-end-radius: 0;
  border-start-start-radius: 0;
}
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover-pane.sas_components-Popover-__internal__-BasePopover_hide-bottom-border {
  border-bottom: none;
  border-end-end-radius: 0;
  border-end-start-radius: 0;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover-footer {
  padding: var(--popover-padding);
  padding-top: 0;
  border: 1px solid var(--popover-bd);
  border-top: none;
  font-size: var(--popover-font-size-m);
  display: flex;
  justify-content: flex-end;
  border-end-end-radius: var(--popover-border-radius);
  border-end-start-radius: var(--popover-border-radius);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_arrow {
  position: absolute;
  display: flex;
  overflow: hidden;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_arrow::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: var(--popover-bg);
  transform: rotate(45deg);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=top] > .sas_components-Popover-__internal__-BasePopover_arrow,
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=bottom] > .sas_components-Popover-__internal__-BasePopover_arrow {
  height: calc(22px - 8px);
  width: calc(22px - 2px);
  justify-content: center;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=left] > .sas_components-Popover-__internal__-BasePopover_arrow,
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=right] > .sas_components-Popover-__internal__-BasePopover_arrow {
  height: calc(22px - 2px);
  width: calc(22px - 8px);
  align-items: center;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=top] > .sas_components-Popover-__internal__-BasePopover_arrow {
  bottom: calc(-1 * 22px + 9px);
  align-items: flex-end;
}
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=top] > .sas_components-Popover-__internal__-BasePopover_arrow::before {
  bottom: 50%;
  border-bottom: 1px solid var(--popover-arrow-bd);
  border-right: 1px solid var(--popover-arrow-bd);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=bottom] > .sas_components-Popover-__internal__-BasePopover_arrow {
  top: calc(-1 * 22px + 9px);
  align-items: flex-start;
}
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=bottom] > .sas_components-Popover-__internal__-BasePopover_arrow::before {
  top: 50%;
  border-top: 1px solid var(--popover-arrow-bd);
  border-left: 1px solid var(--popover-arrow-bd);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=left] > .sas_components-Popover-__internal__-BasePopover_arrow {
  right: calc(-1 * 22px + 9px);
  justify-content: flex-end;
}
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=left] > .sas_components-Popover-__internal__-BasePopover_arrow::before {
  right: 50%;
  border-top: 1px solid var(--popover-arrow-bd);
  border-right: 1px solid var(--popover-arrow-bd);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=right] > .sas_components-Popover-__internal__-BasePopover_arrow {
  left: calc(-1 * 22px + 9px);
  justify-content: flex-start;
}
.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_popover[data-popper-placement^=right] > .sas_components-Popover-__internal__-BasePopover_arrow::before {
  left: 50%;
  border-bottom: 1px solid var(--popover-arrow-bd);
  border-left: 1px solid var(--popover-arrow-bd);
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_viewport-overlay {
  position: fixed;
  pointer-events: auto;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.sas-va-sdk .sas_components-Popover-__internal__-BasePopover_block-pointer-events {
  pointer-events: none;
}