.sas-va-sdk .sas_components-DatePicker-DatePicker_container {
  display: inline-flex;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_input {
  flex-grow: 1;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_icon-button {
  padding: 0 0.25rem;
  border-start-start-radius: 0;
  border-end-start-radius: 0;
}

.sas-va-sdk[dir="ltr"] .sas_components-DatePicker-DatePicker_editable-icon-button {
  border-left: 0;
}

.sas-va-sdk[dir="rtl"] .sas_components-DatePicker-DatePicker_editable-icon-button {
  border-right: 0;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_footer {
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 0.5rem;
  justify-content: flex-end;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_form {
  background-color: var(--datefield-bg);
  border-color: var(--datefield-bd);
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_input-enabled > input {
  color: var(--datefield-fg);
}
.sas-va-sdk .sas_components-DatePicker-DatePicker_input-enabled > input:focus {
  border-width: 0;
  outline: none;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_input-error > input {
  border-width: 0;
  color: var(--datefield-active-fg);
  outline: none;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_form {
  display: inline-flex;
  border-width: 1px;
  border-right: 1px solid var(--datefield-noneditable-bg);
  width: 100%;
}
.sas-va-sdk .sas_components-DatePicker-DatePicker_form.sas_components-DatePicker-DatePicker_active {
  border-style: solid;
  outline: var(--datefield-focus-outline);
  outline-offset: var(--datefield-focus-outline-offset);
}
.sas-va-sdk .sas_components-DatePicker-DatePicker_form.sas_components-DatePicker-DatePicker_active:not(.sas_components-DatePicker-DatePicker_disabled) {
  border-color: var(--datefield-active-bd);
}
.sas-va-sdk .sas_components-DatePicker-DatePicker_form.sas_components-DatePicker-DatePicker_error {
  border-color: var(--datefield-error-bd);
  border-style: solid;
}
.sas-va-sdk .sas_components-DatePicker-DatePicker_form.sas_components-DatePicker-DatePicker_warning {
  border-color: var(--datefield-warning-bd);
  border-style: solid;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_disabled {
  background-color: var(--datefield-disabled-bg);
  border-color: var(--datefield-disabled-bd);
  color: var(--datefield-disabled-fg);
  cursor: default;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_reset-button {
  flex-shrink: 0;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_date-input {
  flex-grow: 1;
}
.sas-va-sdk .sas_components-DatePicker-DatePicker_date-input > input {
  width: 100%;
  min-height: 1.25rem;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_error-button {
  flex-shrink: 0;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_message, .sas-va-sdk .sas_components-DatePicker-DatePicker_warning-message, .sas-va-sdk .sas_components-DatePicker-DatePicker_alert-message {
  padding: var(--datefield-message-pd-squish-s);
  font-size: var(--datefield-message-font-size-xs);
  white-space: normal;
  overflow-wrap: anywhere;
  word-break: break-word;
  display: block;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_alert-message {
  background-color: var(--datefield-message-bg);
  border: var(--datefield-message-alert-bd);
  color: var(--datefield-message-alert-fg);
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_warning-message {
  background-color: var(--datefield-message-bg);
  border: var(--datefield-message-warning-bd);
  color: var(--datefield-message-warning-fg);
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_date-time-container {
  justify-content: center;
  align-items: center;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_select {
  min-width: 10rem;
}

.sas-va-sdk .sas_components-DatePicker-DatePicker_picker-container {
  height: 3.313rem;
}