.sas-va-sdk .sas_ObjectToolbar-ObjectToolbar_wrapper {
  position: absolute;
  right: 2px;
  top: 2px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.sas-va-sdk .sas_ObjectToolbar-ObjectToolbar_toolbar {
  border: 1px solid var(--object-toolbar-bd);
  background-color: var(--object-toolbar-bg);
}
.sas-va-sdk .sas_ObjectToolbar-ObjectToolbar_button {
  border-width: 0;
  padding: 0 0.25rem;
}
