@keyframes novaSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes novaFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes novaFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes novaSlideUpFadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideDownFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(8px);
  }
}
@keyframes novaSlideLeftFadeIn {
  from {
    opacity: 0;
    transform: translateX(16px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideLeftFadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-16px);
  }
}
@keyframes novaPopIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes novaPopOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}
@keyframes novaSlideInStart {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutStart {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}
@keyframes novaSlideInEnd {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutEnd {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100px);
  }
}
@keyframes novaSlideInTop {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}
@keyframes novaSlideInBottom {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutBottom {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
@keyframes novaBounceInLeft {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }
  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }
  100% {
    transform: none;
  }
}
.sas-va-sdk .sas_components-MessageToast-MessageToast_container {
  position: absolute;
  z-index: 100;
  width: 17rem;
  max-width: 20rem;
  padding: 32px;
  overflow: hidden;
  box-sizing: content-box;
}

.sas-va-sdk .sas_components-MessageToast-MessageToast_container-inner {
  display: flex;
  align-items: center;
  font-size: var(--toast-font-size-m);
  box-shadow: var(--toast-sh);
  padding: var(--toast-spacing-m);
  background-color: var(--toast-bg);
  border: 1px solid var(--toast-bd);
  border-radius: 2px;
  will-change: transform;
  position: relative;
}

.sas-va-sdk .sas_components-MessageToast-MessageToast_entering_start_start, .sas-va-sdk .sas_components-MessageToast-MessageToast_entering_start_center, .sas-va-sdk .sas_components-MessageToast-MessageToast_entering_start_end {
