@keyframes novaSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes novaFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes novaFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes novaSlideUpFadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideDownFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(8px);
  }
}
@keyframes novaSlideLeftFadeIn {
  from {
    opacity: 0;
    transform: translateX(16px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideLeftFadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-16px);
  }
}
@keyframes novaPopIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes novaPopOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}
@keyframes novaSlideInStart {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutStart {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}
@keyframes novaSlideInEnd {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes novaSlideOutEnd {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100px);
  }
}
@keyframes novaSlideInTop {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutTop {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}
@keyframes novaSlideInBottom {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes novaSlideOutBottom {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(100px);
  }
}
@keyframes novaBounceInLeft {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }
  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }
  100% {
    transform: none;
  }
}
.sas-va-sdk .sas_components-ObjectMarker-ObjectMarker_object-marker {
  display: inline-flex;
  max-width: 100%;
  align-items: center;
}
.sas-va-sdk .sas_components-ObjectMarker-ObjectMarker_object-marker:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.sas-va-sdk .sas_components-ObjectMarker-ObjectMarker_object-marker-link {
  cursor: pointer;
}

.sas-va-sdk[dir="ltr"] .sas_components-ObjectMarker-ObjectMarker_label-spacing {
  padding-right: var(--object-marker-spacing-xs);
}

.sas-va-sdk[dir="rtl"] .sas_components-ObjectMarker-ObjectMarker_label-spacing {
  padding-left: var(--object-marker-spacing-xs);
}

.sas-va-sdk .sas_components-ObjectMarker-ObjectMarker_disabled {
  cursor: default;
}

.sas-va-sdk .sas_components-ObjectMarker-ObjectMarker_icon {
  flex-shrink: 0;
  padding: var(--object-marker-spacing-xs);
}

@keyframes sas_components-ObjectMarker-ObjectMarker_animate_spin {
  0% {
    transform: rotate(0deg);
    transform-origin: 50%;
  }
  100% {
    transform: rotate(360deg);
    transform-origin: 50%;
  }
}
.sas-va-sdk .sas_components-ObjectMarker-ObjectMarker_animatedInProgress {
  animation-name: sas_components-ObjectMarker-ObjectMarker_animate_spin;
  animation-duration: calc(700ms * var(--nova-animation-duration-factor, 1));
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  z-index: 1;
}