.sas-va-sdk .sas_PromptHeader-PromptHeader_emptyText {
  width: 100%;
  text-align: center;
  font-size: 0.7912rem;
  line-height: 2.75rem;
  min-height: 2.75rem;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_emptyText.sas_PromptHeader-PromptHeader_vertical {
  text-align: center;
  line-height: 1.3;
  max-width: 75%;
  align-self: center;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_header {
  position: relative;
  flex-shrink: 0;
  background-color: var(--prompt-header-bg);
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_header.sas_PromptHeader-PromptHeader_vertical {
  max-width: 50%;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_headerContent {
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: row;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_headerContent.sas_PromptHeader-PromptHeader_vertical {
  flex-direction: column;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_animateOpen {
  overflow: hidden;
  animation: sas_PromptHeader-PromptHeader_slideIn 0.5s ease-in;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_animateOpen.sas_PromptHeader-PromptHeader_vertical {
  animation: sas_PromptHeader-PromptHeader_fadeIn 0.3s ease-in;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_animateClose {
  overflow: hidden;
  animation: sas_PromptHeader-PromptHeader_slideOut 0.5s ease-out;
}
.sas-va-sdk .sas_PromptHeader-PromptHeader_animateClose.sas_PromptHeader-PromptHeader_vertical {
  animation: sas_PromptHeader-PromptHeader_fadeOut 0.3s ease-out;
}
@keyframes sas_PromptHeader-PromptHeader_slideIn {
  from {
    max-height: 0;
  }
  to {
    max-height: 100%;
  }
}
@keyframes sas_PromptHeader-PromptHeader_slideOut {
  from {
    max-height: 100%;
  }
  to {
    max-height: 0;
  }
}
@keyframes sas_PromptHeader-PromptHeader_fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes sas_PromptHeader-PromptHeader_fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
