.sas-va-sdk html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}
.sas-va-sdk body {
  margin: 0;
}
.sas-va-sdk main {
  display: block;
}
.sas-va-sdk h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
.sas-va-sdk hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}
.sas-va-sdk pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}
.sas-va-sdk a {
  background-color: transparent;
}
.sas-va-sdk abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}
.sas-va-sdk b,
.sas-va-sdk strong {
  font-weight: bolder;
}
.sas-va-sdk code,
.sas-va-sdk kbd,
.sas-va-sdk samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}
.sas-va-sdk small {
  font-size: 80%;
}
.sas-va-sdk sub,
.sas-va-sdk sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
.sas-va-sdk sub {
  bottom: -0.25em;
}
.sas-va-sdk sup {
  top: -0.5em;
}
.sas-va-sdk img {
  border-style: none;
}
.sas-va-sdk button,
.sas-va-sdk input,
.sas-va-sdk optgroup,
.sas-va-sdk select,
.sas-va-sdk textarea {
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}
.sas-va-sdk button,
.sas-va-sdk input {
  /* 1 */
  overflow: visible;
}
.sas-va-sdk button,
.sas-va-sdk select {
  /* 1 */
  text-transform: none;
}
.sas-va-sdk button,
.sas-va-sdk [type=button],
.sas-va-sdk [type=reset],
.sas-va-sdk [type=submit] {
  -webkit-appearance: button;
}
.sas-va-sdk button::-moz-focus-inner,
.sas-va-sdk [type=button]::-moz-focus-inner,
.sas-va-sdk [type=reset]::-moz-focus-inner,
.sas-va-sdk [type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
.sas-va-sdk button:-moz-focusring,
.sas-va-sdk [type=button]:-moz-focusring,
.sas-va-sdk [type=reset]:-moz-focusring,
.sas-va-sdk [type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
.sas-va-sdk fieldset {
  padding: 0.35em 0.75em 0.625em;
}
.sas-va-sdk legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}
.sas-va-sdk progress {
  vertical-align: baseline;
}
.sas-va-sdk textarea {
  overflow: auto;
}
.sas-va-sdk [type=checkbox],
.sas-va-sdk [type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}
.sas-va-sdk [type=number]::-webkit-inner-spin-button,
.sas-va-sdk [type=number]::-webkit-outer-spin-button {
  height: auto;
}
.sas-va-sdk [type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}
.sas-va-sdk [type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
.sas-va-sdk ::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}
.sas-va-sdk details {
  display: block;
}
.sas-va-sdk summary {
  display: list-item;
}
.sas-va-sdk template {
  display: none;
}
.sas-va-sdk [hidden] {
  display: none;
}
.sas-va-sdk button,
.sas-va-sdk input,
.sas-va-sdk optgroup,
.sas-va-sdk select,
.sas-va-sdk textarea {
  line-height: inherit;
}
.sas-va-sdk h1,
.sas-va-sdk h2,
.sas-va-sdk h3,
.sas-va-sdk h4,
.sas-va-sdk h5,
.sas-va-sdk h6 {
  line-height: inherit;
  margin: 0;
}
.sas-va-sdk ul,
.sas-va-sdk ol {
  margin: 0;
}
.sas-va-sdk hr {
  margin: 0;
}
.sas-va-sdk a {
  color: inherit;
}
.sas-va-sdk :focus {
  outline: none;
}

.sas-va-sdk ::-webkit-scrollbar {
  width: 16px;
  height: 16px;
  background-color: transparent;
}
.sas-va-sdk ::-webkit-scrollbar-corner {
  background-color: transparent;
}
.sas-va-sdk ::-webkit-scrollbar-button {
  display: none;
}
.sas-va-sdk ::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-bg, #788497);
  background-clip: padding-box;
  border-radius: 8px;
  border-color: transparent;
  border-style: solid;
  border-width: 6px;
  min-height: 24px;
}

.sas-va-sdk *:hover::-webkit-scrollbar {
  background-color: var(--scrollbar-hover-track-bg);
}

.sas-va-sdk *:hover::-webkit-scrollbar-thumb {
  border-width: 4px;
}

.sas-va-sdk ::selection {
  background-color: var(--selection-bg);
  color: var(--selection-fg);
}

.sas-va-sdk {
  font-family: "AvenirNext", Arial, Helvetica, Verdana, "Open Sans", sans-serif;
}