.sas-va-sdk .sas_components-Heading-Heading_heading {
  display: flex;
  align-items: flex-start;
  color: var(--heading-fg);
  font-weight: var(--heading-font-weight-default);
  max-width: 100%;
  font-size: var(--heading-font-size);
  line-height: var(--heading-line-height);
}
.sas-va-sdk .sas_components-Heading-Heading_heading:focus {
  outline: var(--heading-focus);
}

.sas-va-sdk .sas_components-Heading-Heading_text {
  min-width: 0;
  white-space: pre-wrap;
  overflow-wrap: anywhere;
  word-break: break-word;
}

.sas-va-sdk .sas_components-Heading-Heading_nowrap .sas_components-Heading-Heading_text {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
  overflow-wrap: unset;
  word-break: unset;
}

.sas-va-sdk .sas_components-Heading-Heading_icon {
  flex-shrink: 0;
  height: var(--heading-font-size);
  width: var(--heading-font-size);
}
.sas-va-sdk[dir="ltr"] .sas_components-Heading-Heading_icon:not(.sas_components-Heading-Heading_icon-only) {
  margin: auto 0.25rem auto 0;
}
.sas-va-sdk[dir="rtl"] .sas_components-Heading-Heading_icon:not(.sas_components-Heading-Heading_icon-only) {
  margin: auto 0 auto 0.25rem;
}